@charset "utf-8";
.vibe{
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
.vibe h2{
  font-size: 20px;
  font-weight: normal;
  line-height: 60px;
}
.vibe dl{
  width: 100%;
  padding: 30px 60px;
  box-sizing: border-box;
  border: 1px solid #d31811;
  background: linear-gradient(to right, rgb(215, 67, 87), rgb(241,79,58) 59%, rgb(239, 100, 47));
  border-radius: 5px;
}
.vibe dt{
  display: inline-block;
  line-height: 30px;
  width: 50px;
  margin-right: 10px;
}
.vibe dd{
  display: inline-block;
}
.vibe input{
  width: 200px;
  height: 35px;
  margin: 5px 5px 5px 0px;
  padding: 0 5px;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
}
.vibe input:focus{
  outline-style:none; 
  border-bottom:1px solid rgba(0,0,0,.2);
  box-sizing: border-box;
}
.vibe button[type="submit"]{
  /* background: linear-gradient(135deg, #fe0929 0%,#a805ad 50%,#7601f9 100%); */
  background: rgba(0,0,0,.8);
  color: #fff;
  padding: 6px 50px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  margin: 20px;
}
.vibe button[type="submit"]:hover{
  background: rgba(255,255,255,.6);
  color: #000;
  font-weight: bold;
}
.movie_list li img{
  width: 100%;
}

.movie_list li p{
  text-align: left;
  font-size: 18px;
  line-height: 30px;
}
.movie_list{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%; max-width: 1240px;
  margin: 0 auto;
  margin-top: 50px;
}
.movie_list li{
  width: 100%; max-width: calc(1240px / 5);
  height: 550px;
  box-sizing: border-box;
  padding: 15px 20px;
  position: relative;
  margin-bottom: 40px;
}
.movie_list li p:first-of-type{
  display: inline-block;
  padding: 0px 10px;
  border-radius: 20px;
  margin-bottom: 10px;
  border: 1px solid #d31811;
  background: linear-gradient(to right, rgb(215, 67, 87), rgb(241,79,58) 59%, rgb(239, 100, 47));

}
.movie_list li p:nth-of-type(n+3){
  font-size: 14px;
}

.trash{
  display: inline-block;
  font-size: 24px;
  color: rgba(0,0,0,.8);
  padding: 12px 14px;
  background: rgba(255,255,255,.9);
  border-radius: 50%;
  margin-top: 10px;
  position:absolute;
  bottom:0px;
}
.trash:hover{
  background: linear-gradient(to right, rgb(215, 67, 87), rgb(241,79,58) 59%, rgb(239, 100, 47));
  color: rgba(255,255,255,.9);
}
.swiper{
  width: 100%;
  height: 50px;
  margin: 30px 0px;
}
.swiper-slide{
  text-align: center;
  font-size: 18px;
  color: #000;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.swiper-button-next:after,
.swiper-button-prev:after{
  color: #000;
  font-size: 16px !important;
}
.swiper-scrollbar-drag{
  color: #ed3124;
  background: #ed3124;
}
.text_plus{
  display: block;
  width: 80%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-bottom: 5px;
}
